import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Typography
} from '@mui/material';
import { TRANSPORTATION_PRICES } from '../constants/prices';

const TransportationSelector = ({ transportOption, setTransportOption }) => {
  const { t } = useTranslation();

  const transportOptions = [
    { value: 'public', label: t('transportation.public'), pricePerDay: TRANSPORTATION_PRICES.PUBLIC },
    { value: 'taxi', label: t('transportation.taxi'), pricePerDay: TRANSPORTATION_PRICES.TAXI }
  ];

  const handleChange = (event) => {
    const selected = transportOptions.find(option => option.value === event.target.value);
    setTransportOption(selected);
  };

  return (
    <Box sx={{ mb: 4 }}>
      <FormControl fullWidth>
        <InputLabel id="transport-select-label">
          {t('transportation.label')}
        </InputLabel>
        <Select
          labelId="transport-select-label"
          value={transportOption ? transportOption.value : ''}
          label={t('transportation.label')}
          onChange={handleChange}
        >
          {transportOptions.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label} - {option.pricePerDay.toLocaleString()}/day
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <Typography 
        variant="caption" 
        sx={{ 
          display: 'block', 
          mt: 1, 
          color: 'text.secondary',
          fontSize: '0.75rem',
          lineHeight: 1.4
        }}
      >
        {t('transportation.tip')}
      </Typography>
    </Box>
  );
};

export default TransportationSelector;
