import React, { useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { 
  FormControl, 
  Select, 
  MenuItem, 
  InputLabel,
  Box
} from '@mui/material';

const LanguageSelector = () => {
  const { i18n, t } = useTranslation();

  useEffect(() => {
    // Get browser language and normalize it to just the language code
    const browserLang = navigator.language.split('-')[0];
    // Set language to browser's language if supported, otherwise default to English
    const initialLang = ['en', 'fr'].includes(browserLang) ? browserLang : 'en';
    i18n.changeLanguage(initialLang);
  }, [i18n]);

  const handleLanguageChange = (event) => {
    i18n.changeLanguage(event.target.value);
  };

  return (
    <Box sx={{ mb: 3 }}>
      <FormControl fullWidth>
        <InputLabel id="language-select-label">
          {t('language.label')}
        </InputLabel>
        <Select
          labelId="language-select-label"
          value={i18n.language.split('-')[0]} // Normalize the language code
          label={t('language.label')}
          onChange={handleLanguageChange}
        >
          <MenuItem value="en">{t('language.en')}</MenuItem>
          <MenuItem value="fr">{t('language.fr')}</MenuItem>
        </Select>
      </FormControl>
    </Box>
  );
};

export default LanguageSelector;
