import axios from 'axios';

const BASE_URL = process.env.REACT_APP_OPEN_EXCHANGE_RATES_BASE_URL;
const API_KEY = process.env.REACT_APP_OPEN_EXCHANGE_RATES_API_KEY;

export const fetchExchangeRates = async (baseCurrency = 'JPY') => {
  try {
    // The Exchange Rate API endpoint structure
    const response = await axios.get(`${BASE_URL}/${API_KEY}/latest/${baseCurrency}`);
    
    // The API returns rates under conversion_rates
    const rates = response.data.conversion_rates || {};
    
    return {
      JPY: rates.JPY || 1,
      USD: rates.USD,
      EUR: rates.EUR,
      GBP: rates.GBP,
      AUD: rates.AUD,
      CAD: rates.CAD
    };
  } catch (error) {
    console.error('Error fetching exchange rates:', error);
    // Return null values as fallback
    return {
      JPY: 1,
      USD: null,
      EUR: null,
      GBP: null,
      AUD: null,
      CAD: null
    };
  }
};
