import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box,
  Typography,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow
} from '@mui/material';

const CostBreakdown = ({
  duration,
  accommodation,
  groupSize,
  foodOption,
  transportOption,
  activityOption,
  flightCost,
  selectedCurrency,
  exchangeRate
}) => {
  const { t } = useTranslation();

  const calculateTotal = () => {
    if (!accommodation || !foodOption || !transportOption || !activityOption) {
      return 0;
    }

    const accommodationTotal = accommodation.pricePerNight * duration;
    const foodTotal = foodOption.pricePerDay * duration;
    const transportTotal = transportOption.pricePerDay * duration;
    const activityTotal = activityOption.pricePerDay * duration;
    
    return accommodationTotal + foodTotal + transportTotal + activityTotal + flightCost;
  };

  const formatCurrency = (amount) => {
    const convertedAmount = amount * exchangeRate;
    return `${selectedCurrency.symbol}${Math.round(convertedAmount).toLocaleString()}`;
  };

  const total = calculateTotal();
  const perPerson = total / groupSize;

  const costs = [
    { label: t('breakdown.accommodation'), value: accommodation?.pricePerNight * duration || 0 },
    { label: t('breakdown.food'), value: foodOption?.pricePerDay * duration || 0 },
    { label: t('breakdown.transportation'), value: transportOption?.pricePerDay * duration || 0 },
    { label: t('breakdown.activities'), value: activityOption?.pricePerDay * duration || 0 },
    { label: t('breakdown.flight'), value: flightCost }
  ];

  return (
    <Box>
      <Typography variant="h6" gutterBottom>
        {t('breakdown.title')}
      </Typography>
      <TableContainer component={Paper}>
        <Table>
          <TableBody>
            {costs.map((cost) => (
              <TableRow key={cost.label}>
                <TableCell>{cost.label}</TableCell>
                <TableCell align="right">{formatCurrency(cost.value)}</TableCell>
              </TableRow>
            ))}
            <TableRow>
              <TableCell sx={{ fontWeight: 'bold' }}>
                {t('breakdown.total')}
              </TableCell>
              <TableCell align="right" sx={{ fontWeight: 'bold' }}>
                {formatCurrency(total)}
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell>
                {t('breakdown.perPerson')}
              </TableCell>
              <TableCell align="right">
                {formatCurrency(perPerson)}
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell>
                {t('breakdown.daily')}
              </TableCell>
              <TableCell align="right">
                {formatCurrency(total / duration)}
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
};

export default CostBreakdown;
