import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box,
  TextField,
  FormControl,
  Typography
} from '@mui/material';

const FlightCostInput = ({ flightCost, setFlightCost }) => {
  const { t } = useTranslation();

  const handleChange = (event) => {
    const value = event.target.value;
    if (value === '' || /^\d+$/.test(value)) {
      setFlightCost(Number(value) || 0);
    }
  };

  return (
    <Box sx={{ mb: 4 }}>
      <FormControl fullWidth>
        <TextField
          label={t('flight.label')}
          type="text"
          value={flightCost}
          onChange={handleChange}
          InputProps={{
            startAdornment: '',
          }}
        />
      </FormControl>
      <Typography 
        variant="caption" 
        sx={{ 
          display: 'block', 
          mt: 1, 
          color: 'text.secondary',
          fontSize: '0.75rem',
          lineHeight: 1.4
        }}
      >
        {t('flight.tip')}
      </Typography>
    </Box>
  );
};

export default FlightCostInput;
