import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import './i18n';
import { 
  Container, 
  Paper, 
  Typography, 
  Box,
  ThemeProvider,
  createTheme,
  CssBaseline,
  Link,
  Divider
} from '@mui/material';
import TripDurationInput from './components/TripDurationInput';
import AccommodationSelector from './components/AccommodationSelector';
import FoodOptionsSelector from './components/FoodOptionsSelector';
import TransportationSelector from './components/TransportationSelector';
import ActivitySelector from './components/ActivitySelector';
import FlightCostInput from './components/FlightCostInput';
import CostBreakdown from './components/CostBreakdown';
import CurrencySelector from './components/CurrencySelector';
import LanguageSelector from './components/LanguageSelector';

const theme = createTheme({
  palette: {
    primary: {
      main: '#E53935',
    },
    secondary: {
      main: '#1E88E5',
    },
    background: {
      default: '#f5f5f5',
    },
  },
  typography: {
    h4: {
      fontWeight: 600,
    },
    h6: {
      fontWeight: 600,
    },
  },
});

function App() {
  const { t } = useTranslation();
  const [duration, setDuration] = useState(7);
  const [accommodation, setAccommodation] = useState(null);
  const [groupSize, setGroupSize] = useState(1);
  const [foodOption, setFoodOption] = useState(null);
  const [transportOption, setTransportOption] = useState(null);
  const [activityOption, setActivityOption] = useState(null);
  const [flightCost, setFlightCost] = useState(100000);
  const [selectedCurrency, setSelectedCurrency] = useState({ 
    code: 'JPY', 
    symbol: '', 
    name: 'Japanese Yen' 
  });
  const [exchangeRate, setExchangeRate] = useState(1);

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Box sx={{ 
        minHeight: '100vh',
        py: 4,
        background: 'linear-gradient(45deg, #FF9E80 0%, #FF1744 100%)',
      }}>
        <Container maxWidth="md">
          <Paper elevation={3} sx={{ p: 4, borderRadius: 2 }}>
            <Box sx={{ textAlign: 'center', mb: 4 }}>
              <Typography variant="h4" gutterBottom>
                {t('title')}
              </Typography>
              <Typography variant="subtitle1" color="text.secondary">
                {t('subtitle')}
              </Typography>
            </Box>

            <LanguageSelector />

            <Divider sx={{ mb: 4 }} />

            <TripDurationInput 
              duration={duration} 
              setDuration={setDuration} 
            />

            <AccommodationSelector 
              accommodation={accommodation}
              setAccommodation={setAccommodation}
              groupSize={groupSize}
              setGroupSize={setGroupSize}
            />

            <FoodOptionsSelector 
              foodOption={foodOption}
              setFoodOption={setFoodOption}
            />

            <TransportationSelector 
              transportOption={transportOption}
              setTransportOption={setTransportOption}
            />

            <ActivitySelector 
              activityOption={activityOption}
              setActivityOption={setActivityOption}
            />

            <FlightCostInput 
              flightCost={flightCost}
              setFlightCost={setFlightCost}
            />

            <CurrencySelector
              selectedCurrency={selectedCurrency}
              setSelectedCurrency={setSelectedCurrency}
              setExchangeRate={setExchangeRate}
            />

            <Divider sx={{ my: 4 }} />

            <CostBreakdown 
              duration={duration}
              accommodation={accommodation}
              groupSize={groupSize}
              foodOption={foodOption}
              transportOption={transportOption}
              activityOption={activityOption}
              flightCost={flightCost}
              selectedCurrency={selectedCurrency}
              exchangeRate={exchangeRate}
            />

            <Box sx={{ mt: 4, textAlign: 'center' }}>
              <Typography variant="body2" color="text.secondary">
                {t('footer.copyright')}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {t('footer.disclaimer')}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                {t('footer.exchangeRates')}
              </Typography>
              <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
                {t('footer.contact')}{' '}
                <Link href="mailto:contact@japantravelcalc.com">
                  contact@japantravelcalc.com
                </Link>
              </Typography>
            </Box>
          </Paper>
        </Container>
      </Box>
    </ThemeProvider>
  );
}

export default App;
