import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Alert
} from '@mui/material';
import { fetchExchangeRates } from '../services/exchangeRates';

const CurrencySelector = ({ selectedCurrency, setSelectedCurrency, setExchangeRate }) => {
  const { t } = useTranslation();
  const [error, setError] = useState(null);
  const [rates, setRates] = useState({
    JPY: 1,
    USD: null,
    EUR: null,
    GBP: null,
    AUD: null,
    CAD: null
  });

  const currencies = [
    { code: 'JPY', symbol: '', name: 'Japanese Yen', rate: rates.JPY },
    { code: 'USD', symbol: '$', name: 'US Dollar', rate: rates.USD },
    { code: 'EUR', symbol: '', name: 'Euro', rate: rates.EUR },
    { code: 'GBP', symbol: '', name: 'British Pound', rate: rates.GBP },
    { code: 'AUD', symbol: 'A$', name: 'Australian Dollar', rate: rates.AUD },
    { code: 'CAD', symbol: 'C$', name: 'Canadian Dollar', rate: rates.CAD }
  ];

  useEffect(() => {
    const loadExchangeRates = async () => {
      try {
        const newRates = await fetchExchangeRates();
        setRates(newRates);
        setError(null);
        
        // Update the selected currency's rate if it exists
        if (selectedCurrency && selectedCurrency.code) {
          const updatedCurrency = {
            ...selectedCurrency,
            rate: newRates[selectedCurrency.code]
          };
          setSelectedCurrency(updatedCurrency);
        }
      } catch (err) {
        setError('Failed to fetch exchange rates');
        console.error('Error loading exchange rates:', err);
      }
    };

    // Load rates immediately
    loadExchangeRates();

    // Refresh rates every hour
    const interval = setInterval(loadExchangeRates, 3600000);

    return () => clearInterval(interval);
  }, [setSelectedCurrency, selectedCurrency]);

  useEffect(() => {
    setExchangeRate(selectedCurrency.rate || 1);
  }, [selectedCurrency, setExchangeRate]);

  const handleChange = (event) => {
    const selected = currencies.find(currency => currency.code === event.target.value);
    if (selected) {
      setSelectedCurrency({
        ...selected,
        rate: rates[selected.code]
      });
    }
  };

  return (
    <Box sx={{ mb: 4 }}>
      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}
      <FormControl fullWidth>
        <InputLabel id="currency-select-label">
          {t('currency.label')}
        </InputLabel>
        <Select
          labelId="currency-select-label"
          value={selectedCurrency.code}
          label={t('currency.label')}
          onChange={handleChange}
        >
          {currencies.map((currency) => (
            <MenuItem 
              key={currency.code} 
              value={currency.code}
              disabled={currency.code !== 'JPY' && !rates[currency.code]}
            >
              {currency.symbol} - {currency.name}
              {currency.code !== 'JPY' && !rates[currency.code] && ' (Unavailable)'}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </Box>
  );
};

export default CurrencySelector;
