import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Typography,
  Slider
} from '@mui/material';
import { ACCOMMODATION_PRICES } from '../constants/prices';

const AccommodationSelector = ({ accommodation, setAccommodation, groupSize, setGroupSize }) => {
  const { t } = useTranslation();

  const accommodationOptions = [
    { value: 'capsule', label: t('accommodation.capsule'), pricePerNight: ACCOMMODATION_PRICES.CAPSULE },
    { value: 'hostel', label: t('accommodation.hostel'), pricePerNight: ACCOMMODATION_PRICES.HOSTEL },
    { value: 'budget', label: t('accommodation.budget'), pricePerNight: ACCOMMODATION_PRICES.BUDGET },
    { value: 'moderate', label: t('accommodation.moderate'), pricePerNight: ACCOMMODATION_PRICES.MODERATE },
    { value: 'luxury', label: t('accommodation.luxury'), pricePerNight: ACCOMMODATION_PRICES.LUXURY }
  ];

  const handleAccommodationChange = (event) => {
    const selected = accommodationOptions.find(option => option.value === event.target.value);
    setAccommodation(selected);
  };

  const handleGroupSizeChange = (event, newValue) => {
    setGroupSize(newValue);
  };

  return (
    <Box sx={{ mb: 4 }}>
      <FormControl fullWidth sx={{ mb: 2 }}>
        <InputLabel id="accommodation-select-label">
          {t('accommodation.label')}
        </InputLabel>
        <Select
          labelId="accommodation-select-label"
          value={accommodation ? accommodation.value : ''}
          label={t('accommodation.label')}
          onChange={handleAccommodationChange}
        >
          {accommodationOptions.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label} - {option.pricePerNight.toLocaleString()}/night
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      <Box>
        <Typography gutterBottom>
          {t('accommodation.groupSize')}: {groupSize}
        </Typography>
        <Slider
          value={groupSize}
          onChange={handleGroupSizeChange}
          min={1}
          max={8}
          marks
          valueLabelDisplay="auto"
          aria-label={t('accommodation.groupSize')}
        />
      </Box>
    </Box>
  );
};

export default AccommodationSelector;
