import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Typography
} from '@mui/material';
import { FOOD_PRICES } from '../constants/prices';

const FoodOptionsSelector = ({ foodOption, setFoodOption }) => {
  const { t } = useTranslation();

  const foodOptions = [
    { value: 'budget', label: t('food.budget'), pricePerDay: FOOD_PRICES.BUDGET },
    { value: 'moderate', label: t('food.moderate'), pricePerDay: FOOD_PRICES.MODERATE },
    { value: 'luxury', label: t('food.luxury'), pricePerDay: FOOD_PRICES.LUXURY }
  ];

  const handleChange = (event) => {
    const selected = foodOptions.find(option => option.value === event.target.value);
    setFoodOption(selected);
  };

  return (
    <Box sx={{ mb: 4 }}>
      <FormControl fullWidth>
        <InputLabel id="food-select-label">
          {t('food.label')}
        </InputLabel>
        <Select
          labelId="food-select-label"
          value={foodOption ? foodOption.value : ''}
          label={t('food.label')}
          onChange={handleChange}
        >
          {foodOptions.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label} - {option.pricePerDay.toLocaleString()}/day
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <Typography 
        variant="caption" 
        sx={{ 
          display: 'block', 
          mt: 1, 
          color: 'text.secondary',
          fontSize: '0.75rem',
          lineHeight: 1.4
        }}
      >
        {t('food.tip')}
      </Typography>
    </Box>
  );
};

export default FoodOptionsSelector;
