import React from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Typography
} from '@mui/material';
import { ACTIVITY_PRICES } from '../constants/prices';

const ActivitySelector = ({ activityOption, setActivityOption }) => {
  const { t } = useTranslation();

  const activityOptions = [
    { value: 'minimal', label: t('activities.minimal'), pricePerDay: ACTIVITY_PRICES.MINIMAL },
    { value: 'moderate', label: t('activities.moderate'), pricePerDay: ACTIVITY_PRICES.MODERATE },
    { value: 'luxury', label: t('activities.luxury'), pricePerDay: ACTIVITY_PRICES.LUXURY }
  ];

  const handleChange = (event) => {
    const selected = activityOptions.find(option => option.value === event.target.value);
    setActivityOption(selected);
  };

  return (
    <Box sx={{ mb: 4 }}>
      <FormControl fullWidth>
        <InputLabel id="activity-select-label">
          {t('activities.label')}
        </InputLabel>
        <Select
          labelId="activity-select-label"
          value={activityOption ? activityOption.value : ''}
          label={t('activities.label')}
          onChange={handleChange}
        >
          {activityOptions.map((option) => (
            <MenuItem key={option.value} value={option.value}>
              {option.label} - {option.pricePerDay.toLocaleString()}/day
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <Typography 
        variant="caption" 
        sx={{ 
          display: 'block', 
          mt: 1, 
          color: 'text.secondary',
          fontSize: '0.75rem',
          lineHeight: 1.4
        }}
      >
        {t('activities.tip')}
      </Typography>
    </Box>
  );
};

export default ActivitySelector;
